<template>
  <section class="chart">
    <header class="chart-header">
      <h4 class="chart-title mb-5 pb-1">/</h4>
      <main class="row gy-5">
        <div class="col-md-6">
          <BarChart class="custom-skill chart-content" :chartData="chartData" :options="options"></BarChart>
        </div>
        <div class="col-md-6 d-flex justify-content-center align-items-center">
          <div class="chart-card py-4 bg-dark rounded">
            <div class="px-3 mx-3" v-if="$route.name !== &quot;Designer&quot;">
              <h6 class="fw-bold"></h6>
              <p class="text-light pb-4 mb-3">Node.jsPythonPHPJAVARuby</p>
              <h6 class="fw-bold"></h6>
              <p class="text-light pb-4 mb-3">Trelloasana</p>
              <h6 class="fw-bold">UI </h6>
              <p class="text-light mb-0">Adobe XDFigma</p>
            </div>
            <div class="px-3 mx-3" v-else>
              <h6 class="fw-bold"></h6>
              <p class="text-light pb-4 mb-3">Adobe XDPhotoshopIllustratorFigma</p>
            </div>
          </div>
        </div>
      </main>
    </header>
  </section>
</template>

<style lang="scss" scoped>
.custom-skill {
  height: 288px;
}
</style>

<style lang="scss" scoped>
.custom {
  height: 244.5px;
}
</style>

<script>
import { ref } from 'vue'
import { useRoute } from 'vue-router'
import { BarChart } from 'vue-chart-3'
export default {
  components: { BarChart },
  props: {
    apiData: {
      type: Promise,
      default () { return [] }
    }
  },
  setup (props) {
    const apiData = ref(props.apiData)
    const engineerSkills = [
      'Git',
      '',
      '',
      'UI'
    ]
    const designerSkills = [
      'HTMLCSS',
      'jQuery',
      'JavaScript',
      ''
    ]
    const engineerLabels = [
      'Git ',
      '',
      '',
      'UI '
    ]
    const designerLabels = [
      'HTMLCSS',
      'jQuery',
      'JavaScript',
      ''
    ]
    const route = useRoute()
    const skills = (route.name === 'Designer') ? designerSkills : engineerSkills
    const labels = (route.name === 'Designer') ? designerLabels : engineerLabels
    const skillsRex = skills.map((skill) => new RegExp(skill))
    const skillsData = ref([0, 0, 0, 0])
    const dataNum = ref([0])
    apiData.value
      .then((rawData) => {
        rawData.forEach((people) => {
          dataNum.value[0] += 1
          const { skill } = people.first_job
          skillsRex.forEach((skillRex, index) => {
            if (skillRex.test(skill)) {
              skillsData.value[index] += 1
            }
          })
        })
      })
    // chart
    const chartData = {
      labels,
      datasets: [
        {
          maxBarThickness: 32,
          minBarLength: 5,
          data: skillsData.value,
          backgroundColor: '#8E7DFA',
          label: ''
        }
      ]
    }
    const options = {
      indexAxis: 'y',
      scales: {
        y: {
          stacked: true,
          grid: {
            display: false
          }
        },
        x: {
          stacked: true,
          grid: {
            drawTicks: true,
            color: '#6B6783',
            tickColor: '#080231',
            tickLength: 24
          },
          min: 0,
          max: dataNum.value,
          ticks: {
            // ticks
            callback: (value) => `${((value / dataNum.value) * 100).toFixed(0)}%`,
            stepSize: () => dataNum.value * 0.2
          }
        }
      }
    }
    return {
      chartData,
      options
    }
  }
}
</script>
