<template>
  <section class="chart">
    <header class="chart-header">
      <h4 class="chart-title"></h4>
    </header>
    <main class="chart-body"><small class="chart-unit"></small>
      <BarChart class="chart-content ps-3 ms-1" :chartData="chartData" :options="options"></BarChart>
    </main>
  </section>
</template>

<script>
import { ref } from 'vue'
import { BarChart } from 'vue-chart-3'
export default {
  components: { BarChart },
  props: {
    apiData: {
      type: Promise,
      default () { return [] }
    }
  },
  setup (props) {
    const apiData = ref(props.apiData)
    const labels = ['1', '2-3', '3-5', '5-7', '7-9', '10']
    const tenuresRex = labels.map((tenure) => new RegExp(tenure))
    const tenureDatas = ref(labels.map(() => 0))
    apiData.value
      .then((rawData) => {
        rawData.forEach((people) => {
          const jobTenure = people.company.job_tenure
            .replace(' ', '')
            .replace('~', '-')
          tenuresRex.forEach((tenure, index) => {
            if (tenure.test(jobTenure)) {
              tenureDatas.value[index] += 1
            }
          })
        })
      })
    // chart
    const chartData = {
      labels,
      datasets: [{
        maxBarThickness: 48,
        minBarLength: 5,
        data: tenureDatas.value,
        label: ''
      }]
    }
    const options = {
      scales: {
        x: {
          grid: {
            display: false
          },
          ticks: {
            autoSkip: false
          }
        },
        y: {
          grid: {
            drawTicks: true,
            color: '#6B6783',
            tickColor: '#080231',
            tickLength: 24
          },
          min: 0,
          max: 175,
          ticks: {
            stepSize: 30
          }
        }
      }
    }
    return {
      chartData,
      options
    }
  }
}
</script>
